export function formatJson(userDetails){
    var formBody = [];
    for (var property in userDetails) {
        var encodedKey = property;
        var encodedValue = userDetails[property];
        formBody.push(encodedKey + "=" + encodedValue);
    }
    formBody = formBody.join("&");
    return formBody;
}
export async function createCharge(formBody){
    let formattedBody = formatJson(formBody);
    let data = await fetch("https://api.stripe.com/v1/charges", {
        headers: {
            Authorization: "Bearer sk_test_NMvNeiR9TSqWJtyuPt4tgx2v",
            "Content-Type": "application/x-www-form-urlencoded",
        },
        body: formattedBody,
        method: "POST",
        
    })
    .then((response) => response.json())
    .then((json) => {return json})
    .catch((error) => console.error(error))
    return await data;
      // .finally(() => {
      // });
}
